#pageHome {
  display: flex;
  align-items: stretch;
  height: 100vh;

  &.dark {
    background: #333;
    color: #fff;
  }

  aside {
    flex: 7;

    background: #00c3ff;
    color: #fff;

    display: flex;
    flex-direction: column;
    justify-content: center;

    padding: 120px 90px;

    img {
      max-width: 300px;
    }

    strong {
      font: 700 36px 'Poppins', sans-serif;
      line-height: 42px;
      margin-top: 5px;
    }

    p {
      font-size: 24px;
      line-height: 32px;
      color: #f8f8f8;
    }
  }

  @media only screen and (max-width: 800px) {
    aside {
      display: none;
    }
  }
  
  main {
    flex: 8;

    padding: 0 32px;

    display: flex;
    align-items: center;
    justify-content: center;
  }

  .main-content {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 320px;
    align-items: stretch;
    text-align: center;

    > img {
      align-self: center;
      margin-bottom: 20px;
    }

    h2 {
      font-size: 24px;
      margin: 64px 0 24px;
      font-family: 'Poppins', sans-serif;
    }

    p {
      font-size: 14px;
      color: #737380;
      margin-top: 16px;

      a {
        color: #00eeff;
      }
    }
  }

  .redirect-buttom {
    margin-top: 10px;
    height: 50px;
    border-radius: 8px;
    font-weight: 500;
    color: #fff;

    display: flex;
    justify-content: center;
    align-items: center;

    cursor: pointer;
    border: 0;

    transition: filter 0.2s;

    &:hover {
      filter: brightness(0.9);
    }

    img {
      margin-right: 5px;
    }
  }

  .redesky {
    background-color: #eac935;
  }

  .hexagon {
    background-color: #abf2f7;
    color: #000;
  }

  .github {
    background-color: #e9e9e9;
    color: #000;
  }

  .btn1 {
    background-color: #00c3ff;
    margin-bottom: 5px;
  }

  .separator {
    font-size: 14px;
    color: #a8a8b3;

    margin: 5px 0;
    display: flex;
    align-items: center;

    &::before {
      content: '';
      flex: 1;
      height: 1px;
      background: #a8a8b3;
      margin-right: 16px;
    }

    &::after {
      content: '';
      flex: 1;
      height: 1px;
      background: #a8a8b3;
      margin-left: 16px;
    }
  }
}